<template>

  <div class="empleadoExpediente-container">

    <h1>Expedientes Empleados</h1>
    <p>Esta seccin te permite gestionar y ver los registros de los empleados, incluyendo informacin personal y
      expediente digital.</p>

    <div class="empleadoExpediente-search-bar">
      <!-- Mostrar botones solo si idRol === 6 -->
      <template v-if="idRol == 6 || idRol == 9 || idRol == 1 || idRol == 10">
        <button @click="clearFilters" class="btn-red">Limpiar Filtros</button>
        <button @click="openColumnSettingsModal" class="btn-conf">Configurar Columnas</button>

        <button @click="openRegistrarModal" class="btn-registrar">Registrar Nuevo Empleado</button>
        <button class="notification-btn" @click="toggleNotifications" title="Activa y configura tus notificaciones">
          <i class="fas fa-bell"></i>
        </button>
      </template>
    </div>


    <!-- Listado de Empleados -->
    <div class="empleadoExpediente-table-wrapper" v-if="isTableView">

      <table class="empleadoExpediente-client-table">
        <thead>
          <tr>
            <!-- Columna fija: Nombre -->
            <th>Nombre</th>

            <!-- Columnas dinmicas -->
            <th v-for="col in filteredVisibleDynamicColumns" :key="col.key">
              {{ col.label }}
            </th>

            <!-- Columna fija: Estado -->
            <th>Estado</th>

            <!-- Columna fija: Ver Detalles -->
            <th>Ver Detalles</th>
          </tr>

          <!-- Fila de filtros -->
          <tr>
            <th>
              <input v-model="filters.nombre" placeholder="Buscar nombre" />
            </th>

            <th v-for="col in filteredVisibleDynamicColumns" :key="'filter-' + col.key">
              <input v-model="filters[col.key]" :placeholder="`Buscar ${col.label}`" />
            </th>

            <th></th> <!-- Estado no es filtrable aqu -->
            <th></th> <!-- Ver Detalles tampoco -->
          </tr>
        </thead>
        <tbody>
          <tr v-for="(empleado, index) in filteredEmpleados"
            :key="empleado.id_empleado ? empleado.id_empleado + '-' + index : index">
            <!-- Fijo: Nombre completo -->
            <td>{{ empleado.nombre }} {{ empleado.paterno }} {{ empleado.materno }}</td>

            <!-- Dinmicas -->
            <td v-for="col in filteredVisibleDynamicColumns" :key="'val-' + col.key">
              {{ resolveField(empleado, col.key) }}
            </td>

            <!-- Fijo: Estado -->
            <td style="width: 100px;">
              <span :style="getStatusStyle(empleado.statusDocuments, empleado.statusExam)" class="status-circle"
                title="Expiracin de documentos y exmenes"></span>
              <span>&nbsp;&nbsp;</span>
              <span :style="getEstadoStyle(empleado.estadoDocumento, empleado.estadoExam)" class="status-cuadro"
                title="Estado de los documentos y exmenes"></span>
              <span v-if="empleado.statusMedico == 1" title="Este cono indica condicin mdica especial."
                class="status-triangulo"></span>
            </td>

            <!-- Fijo: Ver Detalles -->
            <td>
              <i class="fas fa-eye" @click="openModal(empleado.id)" title="Ver detalles"></i>
              <span>&nbsp;&nbsp;</span>
              <i class="fas fa-trash" v-if="idRol == 6 || idRol == 9 || idRol == 1 || idRol == 10"
                @click="deleteEmpleado(empleado.id)" title="Eliminar empleado"></i>
            </td>
          </tr>
        </tbody>

      </table>
    </div>

    <div class="empleadoExpediente-cards-wrapper" v-else>
      <div class="empleadoExpediente-client-card" v-for="empleado in filteredEmpleados" :key="empleado.id">
        <h3>{{ empleado.nombre }}</h3>
        <p>Email: {{ empleado.correo }}</p>
        <p>Telfono: {{ empleado.telefono }}</p>
        <button @click="openModal(empleado.id)">Ver Detalles</button>
      </div>
    </div>

    <!-- Modal -->
    <PerfilModal :empleado="selectedEmpleado" :isVisible="!!selectedEmpleado" @close="closeModal"
      @add-document="addDocument" @delete-document="deleteDocument" @empleadoActualizado="fetchEmpleados"
      :updateEmpleado="updateEmpleado" :status="selectedEmpleado ? selectedEmpleado.statusDocuments : null"
      :exam="selectedEmpleado ? selectedEmpleado.statusExam : null"
      :estado="selectedEmpleado ? selectedEmpleado.estadoDocumento : null"
      :estadoExam="selectedEmpleado ? selectedEmpleado.estadoExam : null" :idRol="idRol" :idPortal="idPortal"
      :idCliente="idCliente" :idUsuario="idUsuario" />

    <RegistrarEmpleado ref="registrarEmpleado" :idRol="idRol" :idPortal="idPortal" :idCliente="idCliente"
      :idUsuario="idUsuario" @modalClosed="fetchEmpleados" @empleadoActualizado="fetchEmpleados" />
    <!-- Componente Modal -->

    <NotificationModal :isModalOpen="isModalOpen" :idRol="idRol" :idPortal="idPortal" :idCliente="idCliente"
      :idUsuario="idUsuario" @update:isModalOpen="isModalOpen = $event" />
    <ModalColumnSettings v-if="isColumnSettingsModalOpen" :columns="columns" @close="isColumnSettingsModalOpen = false"
      @save="saveColumnSettings" />
  </div>
</template>

<script>
import api from '@/services/apiEmpleados'; // Asegrate de que la ruta sea correcta
import PerfilModal from './componentsPerfil/PerfilModal.vue';  // Asegrate de la ruta correcta
import RegistrarEmpleado from './modales/RegistrarEmpleado.vue';
import NotificationModal from './modales/NotificacionModal.vue';
import ModalColumnSettings from './modales/ModalColumnSettings.vue';
import axios from 'axios';
// Importa el componente del modal
// Importa el componente del modal
import Swal from 'sweetalert2';  // Importar SweetAlert2
export default {
  name: 'empleadoData',
  components: {
    PerfilModal,
    RegistrarEmpleado,
    NotificationModal,
    ModalColumnSettings,
  },
  props: {
    idPortal: {
      type: String,
      default: ''
    },
    idUsuario: {
      type: String,
      default: ''
    },
    idCliente: {
      type: String,
      default: ''
    },
    idRol: {
      type: String,
      default: ''
    },
  },
  data() {
    return {
      visibleDynamicColumns: [], // esta se inicializa al montar
      isColumnSettingsModalOpen: false,
      empleados: [],
      columnasUsuario: [],
      allColumnKeys: [],  // todas las columnas disponibles (de los datos)
      columns: [],
      filters: {},
      selectedEmpleado: null,
      isTableView: true,
      isModalOpen: false,
    };
  },
  computed: {
    dynamicColumns() {
      return this.columns.filter(col => !['nombre', 'estado', 'ver_detalles'].includes(col.key));
    },
    visibleColumns() {  // cambiado de visibleDynamicColumns
      return this.dynamicColumns.filter(col => col.visible);
    },
    filteredVisibleDynamicColumns() {
      return this.dynamicColumns.filter(col => col.visible);
    },
    filteredEmpleados() {
      return this.empleados.filter(emp => {
        return Object.entries(this.filters).every(([key, value]) => {
          if (!value) return true;
          const fieldValue = this.resolveField(emp, key);
          return fieldValue?.toString().toLowerCase().includes(value.toLowerCase());
        });
      });
    }
  },
  methods: {
    async fetchEmpleados() {
      try {
        // Primero obtener las columnas seleccionadas del backend
        await this.columnasSeleccionadas(); //  Esto carga `this.dynamicColumns.value`

        const apiUrl = `${this.$apiUrl}/empleados/documentos`;
        const response = await api.get(apiUrl, {
          params: {
            id_portal: this.idPortal,
            status: 1,
            id_cliente: this.idCliente
          }
        });
        const data = response.data;
        // Cargar empleados
        this.empleados = Array.isArray(data.empleados) ? data.empleados : [];
        // Obtener columnas disponibles
        const columnasDisponibles = Array.isArray(data.columnas) ? data.columnas : [];
        // Mapear columnas con visibilidad segn lo que carg columnasSeleccionadas()
        const columnasFinales = columnasDisponibles.map(col => {
          const visible = this.columnasUsuario.includes(col); //  Usa directamente columnasUsuario

          return {
            key: col,
            label: this.formatColumnLabel(col),
            visible
          };
        });

        this.columns = columnasFinales;

        //  console.log('Columnas visibles:', this.columns.map(c => c.key));
      } catch (error) {
        console.error('Error en fetchEmpleados:', error);
      }
    },
    async columnasSeleccionadas() {
      try {
        const response = await axios.get(`${this.$apiUrl}/configuracion/columnas`, {
          headers: { 'X-CSRF-TOKEN': this.$csrfToken },
          params: {
            id_usuario: this.idUsuario,
            id_portal: this.idPortal,
            id_cliente: this.idCliente,
            modulo: 'empleados'
          }
        });

        if (Array.isArray(response.data?.columnas)) {
          this.columnasUsuario = response.data.columnas; //  Cambia aqu
        } else {
          this.columnasUsuario = []; // por defecto
        }
      } catch (error) {
        console.error('Error al cargar columnas:', error);
        this.columnasUsuario = [];
      }
    },

    async saveColumnSettings(updatedColumns) {
      // 1. Filtra solo las columnas visibles
      const visibles = updatedColumns.filter(col => col.visible);
      // 2. Actualiza la tabla visible en la vista
      this.columns = updatedColumns;
      //console.log(" ~ saveColumnSettings ~ this.columns:", this.columns)
      // 3. Guarda las claves seleccionadas si quieres persistirlas
      try {
        await axios.post(`${this.$apiUrl}/configuracion/columnas`, {
          id_usuario: this.idUsuario,
          id_portal: this.idPortal,
          id_cliente: this.idCliente,
          modulo: 'empleados',
          columnas: visibles.map(col => col.key)
          // Solo las claves de columnas visibles
        });
        await this.columnasSeleccionadas(); // vuelve a obtener la configuracin guardada
        await this.fetchEmpleados();
      } catch (error) {
        console.error('Error al guardar configuracin de columnas:', error);
      }
    },
    resolveField(empleado, key) {
      // 1. Si la clave pertenece a campoExtra
      if (Array.isArray(empleado.campoExtra)) {
        const match = empleado.campoExtra.find(c => c.campo === key || c.nombre === key);
        if (match) return match.valor ?? match.value ?? '--';
      }

      // 2. Soporte para claves anidadas como "domicilio_empleado.calle"
      try {
        return key.split('.').reduce((acc, part) => acc?.[part], empleado) ?? '--';
      } catch {
        return '--';
      }
    },
    enableInputE() {
      this.inputReadonly = false;
    },
    disableInputE() {
      this.inputReadonly = true;
    },
    async deleteEmpleado(idEmpleado) {
      // Primero mostramos un Swal de confirmacin
      const result = await Swal.fire({
        icon: 'warning',
        title: 'Ests seguro?',
        text: 'Los datos del empleado sern eliminados para siempre. Deseas continuar?',
        showCancelButton: true, // Muestra un botn de cancelacin
        confirmButtonText: 'S, eliminar',
        cancelButtonText: 'No, cancelar',
        iconColor: '#d33', // Color del cono de advertencia
        confirmButtonColor: '#3085d6', // Color del botn de confirmar
        cancelButtonColor: '#aaa', // Color del botn de cancelar
      });

      // Si el usuario confirma, eliminamos el empleado
      if (result.isConfirmed) {
        const apiUrl = `${this.$apiUrl}`;
        try {
          const response = await fetch(`${apiUrl}/delempleados/${idEmpleado}`, {
            method: 'DELETE',
            headers: {
              'Content-Type': 'application/json'
              // Puedes agregar Authorization u otros headers si tu API lo requiere
            }
          });

          if (!response.ok) {
            throw new Error('No se pudo eliminar el empleado.');
          }

          Swal.fire({
            icon: 'success',
            title: 'Empleado Eliminado',
            text: 'El empleado ha sido eliminado correctamente.'
          });

          this.fetchEmpleados(); // recarga la lista
          this.$emit('empleadoEliminado', idEmpleado); // para actualizar la vista

        } catch (error) {
          console.error(error);
          Swal.fire({
            icon: 'error',
            title: 'Error',
            text: 'Hubo un error al eliminar al empleado.'
          });
        }
      } else {
        // Si el usuario cancela, mostramos un mensaje de cancelacin (opcional)
        Swal.fire({
          icon: 'info',
          title: 'Eliminacin cancelada',
          text: 'La eliminacin del empleado ha sido cancelada.'
        });
      }
    },
    toggleNotifications() {
      this.isModalOpen = !this.isModalOpen;
    },
    openRegistrarModal() {
      this.$refs.registrarEmpleado.openModal();
    },
    openModal(empleadoId) {
      const empleado = this.empleados.find(emp => emp.id === empleadoId);
      this.selectedEmpleado = empleado ? JSON.parse(JSON.stringify(empleado)) : null;
    },
    closeModal() {
      this.selectedEmpleado = null;
      this.fetchEmpleados(); // Cambia esto
    },
    updateViewMode() {
      this.isTableView = window.innerWidth > 768;
    },

    formatColumnLabel(key) {
      return key
        .split('.')
        .join(' ')
        .replace(/_/g, ' ')
        .replace(/\b\w/g, c => c.toUpperCase());
    },
    getStatusStyle(statusDocuments, statusExam) {
      if (statusDocuments === 'rojo' || statusExam === 'rojo') {
        return { backgroundColor: 'rgba(255, 0, 0, 0.906)', borderRadius: '50%', width: '20px', height: '20px' };
      }
      if (statusDocuments === 'amarillo' || statusExam === 'amarillo') {
        return { backgroundColor: 'rgb(250, 210, 50)', borderRadius: '50%', width: '20px', height: '20px' };
      }
      if (statusDocuments === 'verde' && statusExam === 'verde') {
        return { backgroundColor: 'rgba(12, 232, 34, 0.922)', borderRadius: '50%', width: '20px', height: '20px' };
      }

      return { backgroundColor: 'white', borderRadius: '50%', width: '20px', height: '20px' };
    },
    getEstadoStyle(status, estadoExam) {
      // Si alguno de los dos es rojo, aplica el color rojo
      if (status === 'rojo' || estadoExam === 'rojo') {
        return { backgroundColor: 'rgba(255, 0, 0, 0.906)', width: '20px', height: '20px' };
      }
      // Si alguno de los dos es amarillo, aplica el color amarillo
      if (status === 'amarillo' || estadoExam === 'amarillo') {
        return { backgroundColor: 'rgb(250, 210, 50)', width: '20px', height: '20px' };
      }
      // Si ambos son verdes, aplica el color verde
      if (status === 'verde' && estadoExam === 'verde') {
        return { backgroundColor: '', width: '20px', height: '20px' };
      }
      // Si no se cumple ninguna condicin, aplica el color blanco
      return { backgroundColor: 'white', width: '20px', height: '20px' };
    },

    async updateEmpleado(updatedData) {
      // Actualizar localmente si quieres (opcional)
      const index = this.empleados.findIndex(emp => emp.id === updatedData.id);
      if (index !== -1) {
        this.empleados.splice(index, 1, { ...this.empleados[index], ...updatedData });
      }


    },
    clearFilters() {
      this.filters = {};
    },
    addDocument() {
      if (this.selectedEmpleado) {
        this.selectedEmpleado.documents.push({
          name: 'Nuevo Documento.pdf',
          uploadDate: new Date().toISOString().split('T')[0],
          expiryDate: new Date(new Date().setFullYear(new Date().getFullYear() + 1)).toISOString().split('T')[0]
        });
      }
    },
    openColumnSettingsModal() {
      this.isColumnSettingsModalOpen = true;
    },
    deleteDocument(index) {
      if (this.selectedEmpleado) {
        this.selectedEmpleado.documents.splice(index, 1);
      }
    },
    closeUpdateModal() {
      this.isUpdateModalVisible = false;
      this.selectedDocument = null;
    },
  },
  async mounted() {
    await this.fetchEmpleados(); // Espera a que se carguen las columnas seleccionadas
    this.updateViewMode();
    window.addEventListener('resize', this.updateViewMode);
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.updateViewMode);
  }
};
</script>

