<template>
  <div class="modal-cols-overlay">
    <div class="modal-cols-content">
      <div class="modal-cols-header">
        <h2>Selecciona las columnas que deseas ver</h2>
      </div>
      <div class="modal-cols-controls">
        <button @click="selectAll(true)" style="background: green; color: azure;">Seleccionar todo</button>
        <button @click="selectAll(false)" style="background: orangered; color: azure;">Deseleccionar todo</button>
      </div>
      <div class="modal-cols-grid">
        <div v-for="col in localColumns" :key="col.key" class="modal-cols-checkbox">
          <label>
            <input type="checkbox" v-model="col.visible" />
            {{ col.label }}
          </label>
        </div>
      </div>
      <div class="modal-cols-buttons">
        <button class="modal-cols-btn modal-cols-btn-primary" @click="save">Guardar</button>
        <button class="modal-cols-btn modal-cols-btn-secondary" @click="$emit('close')">Cancelar</button>
      </div>
    </div>
  </div>
</template>




<script>
export default {
  props: {
    columns: Array
  },
  data() {
    return {
      localColumns: JSON.parse(JSON.stringify(this.columns)) // deep copy
    };
  },
  methods: {
    save() {
      this.$emit('save', this.localColumns);
      this.$emit('close');
    },
    selectAll(state) {
      this.localColumns.forEach(col => {
        col.visible = state;
      });
    }
  }
};
</script>
